export default {
	data() {
		return {
			showExplain: false,
			program: {
			},
			answer: '',
			isAnswer: false
		};
	},
	onLoad(query) {
		let program = JSON.parse(query.json)
		this.program = program
	},
	methods: {
		handleSubmit() {
			if(this.answer === this.program.boss.answer) {
				uni.showModal({
					title: '恭喜你，回答正确!',
					content: 'O币数量+3',
					success: function(res) {
						if(res.confirm) {
							wx.redirectTo({
								url: `../home/home?type=+&value=${3}`
							})
						}
					}
				})
			}
		}
	}
}
